import React, {Component} from 'react';


export default class Cell extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: [],
            rows: 10,
            column: 10,
            componentGrid: {},
        };
    }

    // componentDidMount(){
    //     for(let i = 0;i < this.state.rows; i++){
    //         this.state.grid[i] = [];
    //         for(let j = 0;j < this.state.column; j++){
    //             this.state.grid[i][j] = 0;
    //             let item = <div>{0}</div>;
    //             this.setState({
    //                 componentGrid: this.state.componentGrid + item
    //             });
    //         }
    //     }
    //
    //
    // }

    makeGrid(array){
        let grid = [];

        array.map( (item)=>{
            grid.push(<div>{item}</div>);
        });
        return grid;
    }

    render(){
        let array = [1,2,3,4,5,6];
        let grid = this.makeGrid(array);
        return(
            <div className="Grid">
                {
                   grid
                }
            </div>
        );
    }
}